body,
html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

.logo {
  position: fixed;
  z-index: 10;
  overflow: hidden; }
  @media (min-width: 2560px) {
    .logo {
      left: 160px;
      top: 160px; } }
  @media (min-width: 0) and (max-width: 2560px) {
    .logo {
      left: 120px;
      top: 120px; } }
  @media (min-width: 0) and (max-width: 1930px) {
    .logo {
      left: 80px;
      top: 80px; } }
  @media (min-width: 0) and (max-width: 1280px) {
    .logo {
      left: 40px;
      top: 40px; } }
  @media (min-width: 0) and (max-width: 960px) {
    .logo {
      left: 20px;
      top: 20px; } }
  .logo.appear_before div {
    left: -58.5px; }
  .logo.appear_after div {
    -webkit-transition: left 0.8s cubic-bezier(0.8, 0, 0.2, 1);
    -o-transition: left 0.8s cubic-bezier(0.8, 0, 0.2, 1);
    transition: left 0.8s cubic-bezier(0.8, 0, 0.2, 1);
    left: 0; }
  .logo.leave_animation div {
    left: -58.5px; }
  .logo .logos {
    -webkit-transition: 0.8s cubic-bezier(0.8, 0, 0.2, 1);
    -o-transition: 0.8s cubic-bezier(0.8, 0, 0.2, 1);
    transition: 0.8s cubic-bezier(0.8, 0, 0.2, 1); }
    @media (min-width: 2560px) {
      .logo .logos {
        min-height: 75.6px;
        min-width: 67.9px; } }
    @media (min-width: 0) and (max-width: 2560px) {
      .logo .logos {
        min-height: 54px;
        min-width: 48.5px; } }
    @media (min-width: 0) and (max-width: 1280px) {
      .logo .logos {
        min-height: 43.2px;
        min-width: 38.8px; } }
    .logo .logos .to_white {
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 5;
      -webkit-transition: 0.8s cubic-bezier(0.8, 0, 0.2, 1);
      -o-transition: 0.8s cubic-bezier(0.8, 0, 0.2, 1);
      transition: 0.8s cubic-bezier(0.8, 0, 0.2, 1);
      opacity: 0; }
      .logo .logos .to_white img {
        -webkit-filter: invert(100%);
        filter: invert(100%);
        max-width: 100%;
        position: absolute;
        top: 0;
        left: 0; }
    .logo .logos .white {
      overflow: hidden;
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 2; }
      .logo .logos .white img {
        -webkit-filter: invert(100%);
        filter: invert(100%);
        max-width: 100%;
        position: absolute;
        top: 0;
        left: 0; }
    .logo .logos .black {
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 1; }
      .logo .logos .black img {
        -webkit-filter: invert(0%);
        filter: invert(0%);
        max-width: 100%;
        position: absolute;
        top: 0;
        left: 0; }



